import React, { Component } from "react";
import { getBooks } from "./../services/fakeBookService";
import { getGenres } from "./../services/fakeGenreService";
import Like from "./like";
import Pagination from "./pagination";
import ListGroup from "./listGroup";
import { paginate } from "../utils/paginate";
import _ from "lodash";
import "./books.css";

class Books extends Component {
  state = {
    books: [],
    genres: [],
    currentPage: 1,
    pageSize: 5,
    sortColumn: { path: "title", order: "asc" }
  };

  componentDidMount() {
    const genres = [{ _id: "", name: "All" }, ...getGenres()];
    this.setState({ books: getBooks(), genres });
  }

  handleDelete = book => {
    const books = this.state.books.filter(b => b._id !== book._id);
    this.setState({ books });
  };

  handleLike = book => {
    const books = [...this.state.books];
    const index = books.indexOf(book);
    books[index] = { ...books[index] };
    books[index].liked = !books[index].liked;
    this.setState({ books });
  };

  handlePageChange = page => {
    this.setState({ currentPage: page });
  };

  handleGenreSelect = genre => {
    this.setState({ selectedGenre: genre, currentPage: 1 });
  };

  handleSort = path => {
    const sortColumn = { ...this.state.sortColumn };
    if (sortColumn.path === path)
      sortColumn.order = sortColumn.order === "asc" ? "desc" : "asc";
    else {
      sortColumn.path = path;
      sortColumn.order = "asc";
    }
    this.setState({ sortColumn });
  };

  render() {
    const {
      pageSize,
      currentPage,
      books: allBooks,
      genres,
      selectedGenre,
      sortColumn
    } = this.state;

    const filtered =
      selectedGenre && selectedGenre._id
        ? allBooks.filter(b => b.genre._id === selectedGenre._id)
        : allBooks;

    const sorted = _.orderBy(filtered, [sortColumn.path], [sortColumn.order]);

    const books = paginate(sorted, currentPage, pageSize);

    return (
      <div className="row container">
        <div className="col-md-3 mt-5">
          <ListGroup
            items={genres}
            onItemSelect={this.handleGenreSelect}
            selectedItem={selectedGenre}
          />
        </div>
        <div className="col-md-9 mt-5">
          <p className="lead">Showing {filtered.length} books in the store</p>
          <table className="table">
            <thead>
              <tr>
                <th onClick={() => this.handleSort("title")}>Title</th>
                <th onClick={() => this.handleSort("genre.name")}>Genre</th>
                <th onClick={() => this.handleSort("author")}>Author</th>
                <th onClick={() => this.handleSort("price")}>Price</th>
                <th />
                <th />
              </tr>
            </thead>
            <tbody>
              {books.map(book => (
                <tr key={book._id}>
                  <td>{book.title}</td>
                  <td>{book.genre.name}</td>
                  <td>{book.author}</td>
                  <td>{book.price}</td>
                  <td>
                    <Like
                      liked={book.liked}
                      onClick={() => this.handleLike(book)}
                    />
                  </td>
                  <td>
                    <button
                      onClick={() => this.handleDelete(book)}
                      className="btn btn-success btn-sm"
                    >
                      Delete
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
          <Pagination
            className="ml-5"
            itemsCount={filtered.length}
            pageSize={pageSize}
            currentPage={currentPage}
            onPageChange={this.handlePageChange}
          />
        </div>
      </div>
    );
  }
}

export default Books;
